.message {
    margin-bottom: 20px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
}

.imageAndText img {
    width: 48px;

}

.text {
    background-color: #0066cc;
    max-width: 500px;
    min-width: 200px;
    padding: 10px;
    border-radius: 10px 10px 0;
    color: white;
}

.text:after {

}

.name {
    font-weight: 600;
    text-align: right;
    margin-bottom: 5px;
}

.messageText {
    white-space: pre-wrap;
}

.time {
    text-align: right;
    font-size: 10px;
    margin-right: 10px;
    margin-top: 2px;
    font-weight: 600;
}
.block{
    position: relative;
    left: -8px;
    border: 10px solid transparent;
    border-bottom: 10px solid #0066cc;
    border-left: 10px solid #0068cc;
}